/**
 * @file useToolsQueries.ts
 * @description è‡ªå®šä¹‰ Hooksï¼Œç”¨äºå°è£…å·¥å…·é¡µé¢ (ToolsPage) çš„æ•°æ®è·å–å’Œç¼“å­˜é€»è¾‘ã€‚
 *              ä½¿ç”¨ TanStack Query (useQuery) ç®¡ç†å·¥å…·åˆ—è¡¨ã€åˆ†ç±»å’Œæ ‡ç­¾æ•°æ®ã€‚
 */
import { useQuery } from '@tanstack/react-query';
import axios from 'axios';
import { API_BASE_URL } from '../config';

// --- ç±»å‹å®šä¹‰ ---
// Tool ç±»å‹ (ä¸ ToolsPage.tsx ä¸­ä¿æŒä¸€è‡´æˆ–ä»å…±äº«ç±»å‹æ–‡ä»¶å¯¼å…¥)
export interface Tool {
    id: number;
    name: string;
    description: string;
    slug: string;
    screenshot_url?: string | null;
    source_url?: string | null;
    category?: { id: number; name: string };
    tags?: string[];
}

// Category ç±»å‹
export interface Category {
    id: number;
    name: string;
}

// --- æ–°å¢: ToolDetail ç±»å‹å®šä¹‰ ---
// (ä¸ ToolDetailPage.tsx ä¸­ä¿æŒä¸€è‡´)
export interface ToolDetail {
  id: number;
  name: string;
  description: string;
  slug: string;
  content?: string | null;
  screenshot_url?: string | null;
  source_url?: string | null;
  category?: { id: number; name: string };
  tags?: string[];
  is_free?: boolean;
  features?: string[];
  use_cases?: string[];
  pros?: string[];
  cons?: string[];
  pricing_info?: any; // Adjust type if needed
  installation_steps?: string | null;
  created_at: string;
  updated_at: string;
}

// --- API è·å–å‡½æ•° ---

// è·å–åˆ†ç±»åˆ—è¡¨
const fetchCategories = async (): Promise<Category[]> => {
    console.log("[fetchCategories] Fetching categories...");
    const response = await axios.get<Category[]>(`${API_BASE_URL}/api/categories/`);
    // æ·»åŠ æ•°æ®æ ¡éªŒ
    if (!Array.isArray(response.data)) {
        console.error("Invalid categories data format:", response.data);
        throw new Error("æ— æ³•è·å–åˆ†ç±»åˆ—è¡¨ï¼Œæ•°æ®æ ¼å¼é”™è¯¯ã€‚");
    }
    return response.data;
};

// è·å–æ‰€æœ‰æ ‡ç­¾
const fetchTags = async (): Promise<string[]> => {
    console.log("[fetchTags] Fetching all tags...");
    const response = await axios.get<{ tags: string[] }>(`${API_BASE_URL}/api/tools/tags`);
     // æ·»åŠ æ•°æ®æ ¡éªŒ
    if (!response.data || !Array.isArray(response.data.tags)) {
        console.error("Invalid tags data format:", response.data);
        throw new Error("æ— æ³•è·å–æ ‡ç­¾åˆ—è¡¨ï¼Œæ•°æ®æ ¼å¼é”™è¯¯ã€‚");
    }
    return response.data.tags;
};

// è·å–å·¥å…·åˆ—è¡¨ï¼ˆæ”¯æŒç­›é€‰ï¼‰
const fetchTools = async (categoryId: number | 'all', tag: string | 'all'): Promise<Tool[]> => {
    console.log(`[fetchTools] Fetching tools with category: ${categoryId}, tag: ${tag}...`);
    let toolsUrl = `${API_BASE_URL}/api/tools/?`;

    // --- ä¼˜å…ˆä½¿ç”¨æ ‡ç­¾ç­›é€‰ ---
    if (tag !== 'all') {
        toolsUrl += `tag=${encodeURIComponent(tag)}&`;
    } else if (categoryId !== 'all') { // å¦åˆ™ä½¿ç”¨åˆ†ç±»ç­›é€‰
        toolsUrl += `category_id=${categoryId}&`;
    }
    // --- ç»“æŸä¿®æ”¹ ---

    // æŒ‰åˆ›å»ºæ—¶é—´é™åºæ’åº
    toolsUrl += `sort_by=created_at&sort_order=desc`;

    // æ·»åŠ  Cache-Control headers å¯èƒ½ä¸æ˜¯æœ€ä½³å®è·µï¼Œå› ä¸º TanStack Query ä¼šå¤„ç†ç¼“å­˜
    // å¦‚æœç¡®å®éœ€è¦ç»•è¿‡æµè§ˆå™¨ç¼“å­˜ï¼Œå¯ä»¥æ·»åŠ æ—¶é—´æˆ³
    // toolsUrl += `&_=${new Date().getTime()}`;

    const response = await axios.get<{ tools: Tool[]; total: number }>(toolsUrl);
    // æ·»åŠ æ•°æ®æ ¡éªŒ
    if (!response.data || !Array.isArray(response.data.tools)) {
        console.error("Invalid tools data format:", response.data);
        throw new Error("æ— æ³•è·å–å·¥å…·åˆ—è¡¨ï¼Œæ•°æ®æ ¼å¼é”™è¯¯ã€‚");
    }
    return response.data.tools;
};

// --- æ–°å¢: API è·å–å‡½æ•° for Tool Detail ---
const fetchToolDetailBySlug = async (slug: string): Promise<ToolDetail> => {
    // æ·»åŠ  slug æ£€æŸ¥ï¼Œè™½ç„¶ useQuery çš„ enabled ä¹Ÿä¼šå¤„ç†
    if (!slug) {
        throw new Error("Tool slug is required.");
    }
    console.log(`[fetchToolDetailBySlug] Fetching tool detail for slug: ${slug}...`);
    const response = await axios.get<ToolDetail>(`${API_BASE_URL}/api/tools/slug/${slug}`);
    // åŸºæœ¬çš„æ•°æ®æ ¡éªŒ
    if (!response.data || typeof response.data.id !== 'number') {
        console.error("Invalid tool detail data format:", response.data);
        // è€ƒè™‘æ ¹æ®çŠ¶æ€ç æŠ›å‡ºç‰¹å®šé”™è¯¯ï¼Œä¾‹å¦‚ 404
        throw new Error("æ— æ³•è·å–å·¥å…·è¯¦æƒ…ï¼Œæ•°æ®æ ¼å¼é”™è¯¯ã€‚");
    }
    return response.data;
};

// --- è‡ªå®šä¹‰ Hooks ---

/**
 * Hook: useCategories
 * è·å–å¹¶ç¼“å­˜åˆ†ç±»åˆ—è¡¨ã€‚
 * æ•°æ®è¢«è§†ä¸ºé•¿æ—¶é—´æœ‰æ•ˆ (staleTime: Infinity)ï¼Œå› ä¸ºåˆ†ç±»ä¸å¸¸å˜åŠ¨ã€‚
 */
export const useCategories = () => {
    return useQuery<Category[], Error>({
        queryKey: ['categories'], // ç¼“å­˜é”®
        queryFn: fetchCategories, // è·å–æ•°æ®çš„å‡½æ•°
        staleTime: Infinity, // æ•°æ®æ°¸ä¸è¿‡æœŸï¼Œé™¤éæ‰‹åŠ¨ invalidate
        gcTime: Infinity, // ç¼“å­˜æ°¸ä¸å›æ”¶ï¼Œé™¤éæ‰‹åŠ¨ invalidate
        refetchOnWindowFocus: false, // çª—å£èšç„¦æ—¶ä¸é‡æ–°è·å–
    });
};

/**
 * Hook: useTags
 * è·å–å¹¶ç¼“å­˜æ‰€æœ‰å¯ç”¨æ ‡ç­¾çš„åˆ—è¡¨ã€‚
 * æ•°æ®è¢«è§†ä¸ºé•¿æ—¶é—´æœ‰æ•ˆ (staleTime: 1 å°æ—¶)ã€‚
 */
export const useTags = () => {
    return useQuery<string[], Error>({
        queryKey: ['tags'],
        queryFn: fetchTags,
        staleTime: 1000 * 60 * 60, // 1 å°æ—¶å†…æ•°æ®æ–°é²œ
        gcTime: 1000 * 60 * 60 * 2, // 2 å°æ—¶ä¸æ´»è·ƒåå›æ”¶
        refetchOnWindowFocus: false,
    });
};

/**
 * Hook: useTools
 * è·å–å¹¶ç¼“å­˜å·¥å…·åˆ—è¡¨ï¼Œæ ¹æ®é€‰å®šçš„åˆ†ç±»å’Œæ ‡ç­¾è¿›è¡Œç­›é€‰ã€‚
 * å½“ categoryId æˆ– tag å˜åŒ–æ—¶ï¼Œä¼šè‡ªåŠ¨é‡æ–°è·å–æ•°æ®ã€‚
 * 
 * @param {number | 'all'} categoryId é€‰ä¸­çš„åˆ†ç±» ID æˆ– 'all'
 * @param {string | 'all'} tag é€‰ä¸­çš„æ ‡ç­¾æˆ– 'all'
 */
export const useTools = (categoryId: number | 'all', tag: string | 'all') => {
    return useQuery<Tool[], Error>({
        // queryKey åŒ…å«ç­›é€‰æ¡ä»¶ï¼Œå½“å®ƒä»¬å˜åŒ–æ—¶ï¼ŒæŸ¥è¯¢ä¼šè‡ªåŠ¨æ›´æ–°
        queryKey: ['tools', { category: categoryId, tag: tag }], 
        queryFn: () => fetchTools(categoryId, tag),
        // å¯ä»¥è®¾ç½® staleTime å’Œ gcTimeï¼Œä¾‹å¦‚ 5 åˆ†é’Ÿ
        staleTime: 1000 * 60 * 5, // 5 åˆ†é’Ÿ
        gcTime: 1000 * 60 * 10, // 10 åˆ†é’Ÿ
        refetchOnWindowFocus: true, // çª—å£èšç„¦æ—¶é‡æ–°è·å–ï¼ˆå¯é€‰ï¼‰
    });
};

// --- æ–°å¢: è‡ªå®šä¹‰ Hook for Tool Detail ---

/**
 * Hook: useToolDetail
 * è·å–å¹¶ç¼“å­˜å•ä¸ªå·¥å…·çš„è¯¦ç»†ä¿¡æ¯ã€‚
 * å½“ slug å˜åŒ–æ—¶ï¼Œä¼šè‡ªåŠ¨é‡æ–°è·å–æ•°æ®ã€‚
 * 
 * @param {string | undefined} slug å·¥å…·çš„ slug
 */
export const useToolDetail = (slug: string | undefined) => {
    return useQuery<ToolDetail, Error>({
        // queryKey åŒ…å« slugï¼Œç¡®ä¿æ¯ä¸ªå·¥å…·è¯¦æƒ…éƒ½æœ‰ç‹¬ç«‹çš„ç¼“å­˜
        queryKey: ['toolDetail', slug], 
        // åªæœ‰å½“ slug å­˜åœ¨æ—¶æ‰æ‰§è¡ŒæŸ¥è¯¢
        queryFn: () => fetchToolDetailBySlug(slug!), // ä½¿ç”¨ non-null assertion å› ä¸º enabled ç¡®ä¿äº† slug å­˜åœ¨
        enabled: !!slug, // å…³é”®ï¼šåªæœ‰å½“ slug æœ‰æ•ˆï¼ˆé null/undefined/ç©ºå­—ç¬¦ä¸²ï¼‰æ—¶æ‰è§¦å‘æŸ¥è¯¢
        staleTime: 1000 * 60 * 10, // 10 åˆ†é’Ÿå†…æ•°æ®æ–°é²œ
        gcTime: 1000 * 60 * 30, // 30 åˆ†é’Ÿä¸æ´»è·ƒåå›æ”¶
        refetchOnWindowFocus: false, // çª—å£èšç„¦æ—¶ä¸é‡æ–°è·å–è¯¦æƒ…ï¼ˆé€šå¸¸ä¸éœ€è¦ï¼‰
    });
}; 